.footer {
  background-color: #663F3F; /* Color de fondo del footer */
  color: white; /* Color del texto del footer */
  text-align: center;
  padding: 20px 0;
  position: relative;
  bottom: 0;
  width: 100%;
}

.footer-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.footer-columns {
  display: flex;
  justify-content: space-between;
  width: 100%;
}

.footer-column {
  flex: 1;
  margin: 0 20px;
}

.footer-column h3 {
  margin-bottom: 10px;
}

.footer-column ul {
  list-style: none;
  padding: 0;
}

.footer-column ul li {
  margin-bottom: 8px;
}

.footer-column ul li a {
  color: white;
  text-decoration: none;
}

.footer-column ul li a:hover {
  text-decoration: underline;
}

.social-icons {
  display: flex;
  gap: 10px;
}

.social-icons li {
  list-style: none;
}

.social-icons li a {
  color: white;
  font-size: 24px;
}

.footer-logo {
  display: flex;
  align-items: center;
  justify-content: center;
}

.newsletter-image {
  max-width: 100px;
  height: auto;
}

.footer-bottom {
  margin-top: 20px;
}
