/* Estilos globales */
body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f3f3f3;
  padding-top: 80px; /* Ajusta este valor según la altura de tu navbar */
}

/* Estilos para la sección "Nosotros" */
.about-section {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 600px; /* Ajusta la altura según sea necesario */
  background-color: #eaf4f4; /* Color de fondo claro */
}

.about-image {
  flex: 1;
  background-image: url('../assets/cafe.jpg'); /* Ruta a tu imagen */
  background-size: cover;
  background-position: center;
  height: 100%;
  border-radius: 10px;
  z-index: 1; /* Asegúrate de que la imagen esté debajo del texto */
}

.about-text {
  position: absolute;
  right: 0;
  background-color: rgba(255, 255, 255, 0.8); /* Fondo blanco semitransparente */
  padding: 20px;
  margin: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  font-size: 1.2em; /* Aumenta el tamaño de la fuente */
  color: #333;
  z-index: 2; /* Asegúrate de que el texto esté encima de la imagen */
  max-width: 40%; /* Ajusta el ancho máximo del cuadro de texto */
}

.about-text h2 {
  color: #5c4033; /* Color marrón */
  font-size: 2.5em; /* Aumenta el tamaño de la fuente */
}

.about-text p {
  margin: 10px 0;
}

/* Ajustes responsivos */
@media (max-width: 768px) {
  .about-section {
    flex-direction: column;
    height: auto; /* Ajusta la altura automáticamente en pantallas pequeñas */
  }

  .about-image, .about-text {
    flex: unset;
    width: 100%;
    margin: 10px 0;
  }

  .about-text {
    position: relative;
    max-width: 100%; /* Ajusta el ancho máximo del cuadro de texto */
    background-color: rgba(255, 255, 255, 1); /* Fondo blanco sólido en pantallas pequeñas */
  }

  .about-image {
    height: 300px; /* Ajusta la altura de la imagen en pantallas pequeñas */
  }
}
