@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap');

/* Otros estilos globales */

body {
  font-family: 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f3f3f3;
  padding-top: 80px; /* Ajusta este valor según la altura de tu navbar */
}

h2 {
  color: #5c4033; /* Color marrón */
  font-size: 2.5em; /* Aumenta el tamaño de la fuente */
}

.products {
  text-align: center;
  margin: 50px 0;
  padding: 20px;
  background-color: #ffffff;
}

.product-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 20px;
}

.product-item {
  background-color: #fff;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  width: 300px;
  padding: 20px;
  text-align: center;
  transition: transform 0.2s;
  font-size: 1.2em; /* Aumenta el tamaño de la fuente */
}

.product-item:hover {
  transform: scale(1.05);
}

.product-item img {
  width: 100%;
  border-bottom: 1px solid #ddd;
  padding-bottom: 15px;
  margin-bottom: 15px;
}

.navbar {
  background-color: #663F3F; /* Color principal marrón */
  padding: 10px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1000;
}

.navbar-logo {
  height: 50px;
}

.navbar-links {
  display: flex;
  gap: 20px;
}

.navbar-links a {
  color: white;
  text-decoration: none;
  font-size: 20px; /* Aumenta el tamaño de la fuente */
  font-weight: bold;
}

.navbar-links a:hover {
  color: #F7D308; /* Color amarillo */
  text-decoration: underline;
}

footer {
  background-color: #663F3F; /* Color principal marrón */
  color: white;
  text-align: center;
  padding: 20px 0; /* Aumenta el padding para hacer el footer más grande */
  position: relative;
  width: 100%;
  margin-top: auto; /* Asegura que el footer se mantenga en la parte inferior */
}

.container {
  padding: 20px;
}

.home, .about, .contact {
  padding: 20px;
  text-align: center;
}

/* Asegurar que el contenido no se oculte debajo de la barra de navegación fija */
body {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.app-container {
  display: flex;
  flex-direction: column;
  flex: 1;
}

.content {
  flex: 1;
  padding: 20px;
}
