.home {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #F8F4F1; /* Fondo ligero */
    height: 80vh;
    text-align: center;
  }
  
  .home-content {
    color: #4A2C2A; /* Color del texto */
  }
  
  .home-content h1 {
    font-size: 3rem;
    margin-bottom: 1rem;
  }
  
  .home-content p {
    font-size: 1.5rem;
  }
  