.navbar {
  background-color: #663F3F; /* Color marrón para la barra de navegación */
  padding: 20px 0;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed; /* Hacer que la barra de navegación sea fija */
  width: 100%; /* Asegurarse de que ocupa todo el ancho */
  top: 0; /* Fijar en la parte superior */
  z-index: 1000; /* Asegurar que está por encima de otros elementos */
}

.navbar-logo {
  height: 70px;
  cursor: pointer;
  display: block;
  margin-bottom: 20px; /* Espacio entre el logo y las opciones del navbar */
}

.navbar-links {
  display: flex;
  justify-content: center;
  gap: 30px;
}

.navbar-links a {
  color: white;
  text-decoration: none; /* Eliminamos el subrayado por defecto */
  font-size: 22px; /* Aumentar el tamaño de la letra */
  font-weight: bold;
  font-family: 'Roboto', sans-serif; /* Fuente profesional */
  position: relative;
  padding: 5px 0;
}

.navbar-links a::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  display: block;
  margin-top: 5px;
  left: 0;
  background: yellow; /* Color del subrayado */
  transition: width 0.4s ease;
  -webkit-transition: width 0.4s ease;
}

.navbar-links a:hover::after,
.navbar-links a.active::after {
  width: 100%;
}

/* Estilos adicionales para asegurar la eliminación del subrayado */
.navbar-links a:hover,
.navbar-links a:focus,
.navbar-links a:active {
  text-decoration: none !important; /* Fuerza la eliminación del subrayado */
  outline: none; /* Elimina cualquier contorno de enfoque */
}
